import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";

import test1 from './img/production_1.JPG'
import test2 from './img/production_2.JPG'

const ScrollEffectComponent = () => {
    const scrollContainerRef = useRef(null);
    const blueDivRef = useRef(null);
    const redDivRef = useRef(null);

    useEffect(() => {
        const scrollContainer = scrollContainerRef.current;
        const blueDiv = blueDivRef.current;
        const redDiv = redDivRef.current;

        gsap.set(blueDiv, { y: "0%" });
        gsap.set(redDiv, { y: "100%", opacity: 0 });

        const tl = gsap.timeline({ paused: true });
        tl.to(blueDiv, { y: "-100%", duration: 1, ease: "power3.out" }).to(
            redDiv,
            { y: "0%", opacity: 1, duration: 1, ease: "power3.out" },
            "-=1"
        );

        const handleScroll = () => {
            const triggerBottom = scrollContainer.getBoundingClientRect().bottom;
            const windowBottom = window.innerHeight;

            if (triggerBottom <= windowBottom && triggerBottom >= 0) {
                tl.play();
            } else {
                tl.reverse();
            }
        };

        window.addEventListener("scroll", handleScroll);

        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    return (
        <div>
            <div
                ref={scrollContainerRef}
                style={{ position: "relative", height: "100vh", overflow: "hidden" }}
            >
                {/* Добавляем код раздела перед которым хотим добавить анимированные дивы */}
                <div
                    ref={blueDivRef}
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        height: "100%",
                        width: '100%',
                        backgroundImage: `url(${test1})`,
                        backgroundSize: "cover",
                        backgroundRepeat: "no-repeat",
                        backgroundPosition: "center",
                    }}
                >
                    <h1>TEST TEST TEST</h1>
                </div>
                <div
                    ref={redDivRef}
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        height: "100%",
                        width: '100%',
                        backgroundImage: `url(${test1})`,
                        backgroundSize: "cover",
                        backgroundRepeat: "no-repeat",
                        backgroundPosition: "center",
                        position: "absolute",
                        top: "0",
                        zIndex: "1",
                    }}
                >
                    <h1>TEST TEST TEST</h1>
                </div>
            </div>
        </div>
    );
};

export default ScrollEffectComponent;
